import React from 'react';
import {
    DragDropContext,
    Draggable,
    Droppable,
    DropResult,
} from 'react-beautiful-dnd';
import { useForm } from 'react-hook-form';
import { useRecoilState } from 'recoil';
import styled from 'styled-components';
import { IToDoState, toDoState } from './atom';
import Board from './components/Board';
import DragableCard from './components/DragableCard';

const Wrapper = styled.div`
    max-width: 680px;
    width: 100%;
    margin: 0 auto;

    height: 100vh;
`;

const Boards = styled.div`
    display: grid;
    width: 100%;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
`;
const Form = styled.form`
    width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
`;

const Input = styled.input`
    background: none;
    outline: none;
    border: none;
    border-bottom: 1px solid white;
    text-align: center;
    font-size: 18px;
    &::placeholder {
        color: rgba(255, 255, 255, 0.6);
    }
`;

const App = () => {
    const [todos, setTodos] = useRecoilState(toDoState);
    const { register, setValue, handleSubmit } = useForm();
    const onDragEnd = (info: DropResult) => {
        const { destination, source, type } = info;
        if (type === 'Board') {
            if (!destination) return;
            setTodos((prev) => {
                const update = Object.entries(prev);
                const [temp] = update.splice(source.index, 1);
                update.splice(destination?.index, 0, temp);
                const updateList = update.reduce(
                    (r, [k, v]) => ({ ...r, [k]: v }),
                    {},
                );
                localStorage.setItem('todo', JSON.stringify(updateList));
                return updateList;
            });
        } else {
            if (destination === null) {
                setTodos((prev) => {
                    const update = [...prev[source.droppableId]];
                    update.splice(source.index, 1);
                    const updateList = {
                        ...prev,
                        [source.droppableId]: update,
                    };
                    localStorage.setItem('todo', JSON.stringify(updateList));
                    return updateList;
                });
            }
            if (!destination) return;
            if (destination?.droppableId === source.droppableId) {
                setTodos((prev) => {
                    const update = [...prev[source.droppableId]];
                    const taskObj = update[source.index];
                    update.splice(source.index, 1);
                    update.splice(destination?.index, 0, taskObj);
                    const updateList = {
                        ...prev,
                        [source.droppableId]: update,
                    };
                    localStorage.setItem('todo', JSON.stringify(updateList));
                    return updateList;
                });
            }
            if (destination.droppableId !== source.droppableId) {
                setTodos((prev) => {
                    const sourceUpdate = [...prev[source.droppableId]];
                    const targetUpdate = [...prev[destination.droppableId]];
                    const taskObj = sourceUpdate[source.index];
                    sourceUpdate.splice(source.index, 1);
                    targetUpdate.splice(destination.index, 0, taskObj);
                    const updateList = {
                        ...prev,
                        [source.droppableId]: sourceUpdate,
                        [destination.droppableId]: targetUpdate,
                    };
                    localStorage.setItem('todo', JSON.stringify(updateList));
                    return updateList;
                });
            }
        }
    };
    const onSubmit = ({ board }: IToDoState) => {
        setTodos((prev) => {
            const update = {
                ...prev,
                [board + '']: [],
            };
            localStorage.setItem('todo', JSON.stringify(update));
            return update;
        });
        setValue('board', '');
    };
    return (
        <DragDropContext onDragEnd={onDragEnd}>
            <Wrapper>
                <Form onSubmit={handleSubmit(onSubmit)}>
                    <Input
                        {...register('board', { required: true })}
                        type='text'
                        placeholder='write task'
                    />
                </Form>
                <Boards>
                    {Object.keys(todos).map((boardId) => (
                        <Board
                            boardId={boardId}
                            key={boardId}
                            toDos={todos[boardId]}
                        />
                    ))}
                </Boards>
            </Wrapper>
        </DragDropContext>
    );
};

export default App;
