import { Droppable } from 'react-beautiful-dnd';
import { useRecoilState, useSetRecoilState } from 'recoil';
import styled from 'styled-components';
import { ITodo, toDoState } from '../atom';
import { DragableCard } from './DragableCard';
import { useForm } from 'react-hook-form';

const Wrapper = styled.div`
    padding: 10px 0px;
    border-radius: 5px;
    min-height: 200px;
    background-color: ${(props) => props.theme.boardColor};
    display: flex;
    flex-direction: column;
`;
const Title = styled.h2`
    text-align: center;
    font-weight: 600;
    margin-bottom: 10px;
    font-size: 18px;
`;

interface IAreaProps {
    isDraggingOver: boolean;
    isDraggingFromThis: boolean;
}
const Area = styled.div<IAreaProps>`
    background-color: ${(props) =>
        props.isDraggingOver
            ? '#bfc4c7'
            : props.isDraggingFromThis
            ? '#b2bec3'
            : 'transparent'};
    transition: 0.3s;
    flex-grow: 1;
    padding: 20px;
`;
const Form = styled.form`
    width: 100%;
    input {
        width: 100%;
    }
`;

interface IBoardProps {
    toDos: ITodo[];
    boardId: string;
}
interface IForm {
    toDo: string;
}

function Board({ toDos, boardId }: IBoardProps) {
    const setToDos = useSetRecoilState(toDoState);
    const { register, setValue, handleSubmit } = useForm<IForm>();
    const onValid = ({ toDo }: IForm) => {
        const newToDo = {
            id: Date.now(),
            text: toDo,
        };
        setToDos((prev) => {
            const update = {
                ...prev,
                [boardId]: [...prev[boardId], newToDo],
            };
            localStorage.setItem('todo', JSON.stringify(update));
            return update;
        });
        setValue('toDo', '');
    };
    const onRemove = () => {
        setToDos((prev) => {
            const update = Object.entries(prev).filter(
                (target) => target[0] !== boardId,
            );
            const updateList = update.reduce(
                (r, [k, v]) => ({ ...r, [k]: v }),
                {},
            );
            localStorage.setItem('todo', JSON.stringify(updateList));
            return updateList;
        });
    };
    return (
        <Wrapper>
            <Title>{boardId}</Title>
            <Form onSubmit={handleSubmit(onValid)}>
                <input
                    {...register('toDo', { required: true })}
                    type='text'
                    placeholder='add task'
                />
                <button>add</button>
            </Form>
            <Droppable droppableId={boardId}>
                {(magic, snapshot) => (
                    <Area
                        isDraggingOver={snapshot.isDraggingOver}
                        isDraggingFromThis={Boolean(
                            snapshot.draggingFromThisWith,
                        )}
                        ref={magic.innerRef}
                        {...magic.droppableProps}>
                        {toDos.map((toDo, index) => (
                            <DragableCard
                                key={toDo.id}
                                toDoId={toDo.id}
                                toDoText={toDo.text}
                                index={index}
                            />
                        ))}
                        {magic.placeholder}
                    </Area>
                )}
            </Droppable>
        </Wrapper>
    );
}

export default Board;
